@import url(https://fonts.googleapis.com/css?family=Noto+Sans+HK&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:500,600,700&display=swap);
:root {
  --main-blue: rgb(5, 56, 107);
  --lighter-blue: rgb(8, 51, 180);
  --shadow-green: rgb(55, 150, 131);
  --main-green: rgb(91, 219, 149);
  --light-green: rgb(142, 228, 175);
  --white-green: rgb(237, 245, 225);

  --white-smoke: #F9F9F8;

  --dark: rgb(11, 12, 16);
  --shade: rgb(31, 40, 51);
  --iron: rgb(197, 198, 199);
  --splash: rgb(100, 251, 241);
  --pool: rgb(69, 162, 158);

  --feather: rgb(119, 201, 212);
  --marine: rgb(87, 188, 144);
  --forest: rgb(1, 82, 73);
  --sleek-grey: rgb(165, 165, 175);

  --montserrat: 'Montserrat', sans-serif;
  --noto-sans: 'Noto Sans HK', sans-serif;
}

::-moz-selection {
  color: rgb(91, 219, 149);
  color: var(--main-green);
  background: rgb(8, 51, 180);
  background: var(--lighter-blue);
}

::selection {
  color: rgb(91, 219, 149);
  color: var(--main-green);
  background: rgb(8, 51, 180);
  background: var(--lighter-blue);
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

input,
select,
button {
  background: none;
  border: none;
  font-family: inherit;
  color: black;
  outline: none;
}

button:hover,
select:hover {
  cursor: pointer;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  appearance: none;
  -webkit-appearance: none;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.content {
  padding: 1em;
  width: 75vw;
  margin: auto;
  font-family: 'Montserrat', sans-serif;
  font-family: var(--montserrat);
}

.card {
  background: rgb(5, 56, 107);
  background: var(--main-blue);
  color: #F9F9F8;
  color: var(--white-smoke);
  border-radius: 6px;
  margin-bottom: 0.5em;
  padding: 0.5em;

  --shadow: rgba(8, 51, 180, .2);
  box-shadow: 0 10px 15px 0px var(--shadow);
  -moz-box-shadow: 0 10px 15px 0px var(--shadow);
  -webkit-box-shadow: 0 10px 15px 0px var(--shadow);
  -o-box-shadow: 0 10px 15px 0px var(--shadow);
  -ms-box-shadow: 0 10px 15px 0px var(--shadow);
}

.card-title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.65em;
  margin-bottom: 1em;
}

.title-text {
  padding-right: 10px;
  color: rgb(237, 245, 225);
  color: var(--white-green);
}

.title-line {
  flex: auto;
  margin: 0;
  height: 0.5px;
  background-color: rgb(142, 228, 175);
  background-color: var(--light-green);
  border: 0.5px solid rgb(142, 228, 175);
  border: 0.5px solid var(--light-green);
  opacity: 0.4;
}

.form-input {
  background-color: inherit;
  color: rgb(91, 219, 149);
  color: var(--main-green);
  font-family: 'Noto Sans HK', sans-serif;
  font-family: var(--noto-sans);
  font-size: 0.9rem;

  border: 1.5px solid rgb(237, 245, 225);

  border: 1.5px solid var(--white-green);
  border-radius: 3px;

  padding: 0.5em 0.8em;
  line-height: 2rem;
  box-sizing: border-box;
}

.form-input:active, .form-input:hover {
  border-color: rgb(91, 219, 149);
  border-color: var(--main-green);
  background-color: rgb(91, 219, 149);
  background-color: var(--main-green);
  color: rgb(31, 40, 51);
  color: var(--shade);
}

.form-input:focus {
  background-color: rgb(142, 228, 175);
  background-color: var(--light-green);
  border-color: rgb(91, 219, 149);
  border-color: var(--main-green);
  color: rgb(31, 40, 51);
  color: var(--shade);
}

.form-input::-webkit-input-placeholder {
  color: #F9F9F8;
  color: var(--white-smoke);
  opacity: 0.9;
}

.form-input::-moz-placeholder {
  color: #F9F9F8;
  color: var(--white-smoke);
  opacity: 0.9;
}

.form-input:-ms-input-placeholder {
  color: #F9F9F8;
  color: var(--white-smoke);
  opacity: 0.9;
}

.form-input::-ms-input-placeholder {
  color: #F9F9F8;
  color: var(--white-smoke);
  opacity: 0.9;
}

.form-input::placeholder {
  color: #F9F9F8;
  color: var(--white-smoke);
  opacity: 0.9;
}

.form-input:active::-webkit-input-placeholder,
.form-input:hover::-webkit-input-placeholder,
.form-input:focus::-webkit-input-placeholder {
  color: rgb(31, 40, 51);
  color: var(--shade);
  opacity: 0.6;
  font-style: oblique;
}

.form-input:active::-moz-placeholder,
.form-input:hover::-moz-placeholder,
.form-input:focus::-moz-placeholder {
  color: rgb(31, 40, 51);
  color: var(--shade);
  opacity: 0.6;
  font-style: oblique;
}

.form-input:active:-ms-input-placeholder,
.form-input:hover:-ms-input-placeholder,
.form-input:focus:-ms-input-placeholder {
  color: rgb(31, 40, 51);
  color: var(--shade);
  opacity: 0.6;
  font-style: oblique;
}

.form-input:active::-ms-input-placeholder,
.form-input:hover::-ms-input-placeholder,
.form-input:focus::-ms-input-placeholder {
  color: rgb(31, 40, 51);
  color: var(--shade);
  opacity: 0.6;
  font-style: oblique;
}

.form-input:active::placeholder,
.form-input:hover::placeholder,
.form-input:focus::placeholder {
  color: rgb(31, 40, 51);
  color: var(--shade);
  opacity: 0.6;
  font-style: oblique;
}

.form-input.fill {
  width: 100%;
}

/* from https://codepen.io/P1N2O/pen/pyBNzX */

body {
  width: 100vw;
  height: 100vh;
  color: #fff;
  background: linear-gradient(-45deg, var(--splash), var(--main-green));
  background-size: 400% 400%;
  -webkit-animation: Gradient 15s ease infinite;
  animation: Gradient 15s ease infinite;
}

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.group-field-container {
  padding: 1em;
  margin: 5px 0;
}

.group-field-container:hover {
  cursor: pointer;
}

.group-field-container:hover:not(.expanded) {
  background-color: var(--lighter-blue);
}

.group-field-input-wrapper:hover {
  background-color: var(--main-green);
}

.group-field-input-wrapper .form-input {
  font-family: var(--montserrat);
  text-align: center;
  border: none;
  padding: 0;
  margin: 0;
  font-size: 1.2em;

  color: transparent;
  text-shadow: 0 0 0 var(--white-smoke);
  font-style: oblique;
}

.group-field-input-wrapper .form-input::-webkit-input-placeholder {
  color: var(--white-green);
  opacity: 0.8;
}

.group-field-input-wrapper .form-input::-moz-placeholder {
  color: var(--white-green);
  opacity: 0.8;
}

.group-field-input-wrapper .form-input:-ms-input-placeholder {
  color: var(--white-green);
  opacity: 0.8;
}

.group-field-input-wrapper .form-input::-ms-input-placeholder {
  color: var(--white-green);
  opacity: 0.8;
}

.group-field-input-wrapper .form-input::placeholder {
  color: var(--white-green);
  opacity: 0.8;
}

.group-field-input-wrapper:hover .form-input {
  font-style: normal;
  color: var(--main-blue);
  font-weight: 700;
}

.group-field-input-wrapper:hover .form-input::-webkit-input-placeholder {
  opacity: 0;
  font-style: normal;
}

.group-field-input-wrapper:hover .form-input::-moz-placeholder {
  opacity: 0;
  font-style: normal;
}

.group-field-input-wrapper:hover .form-input:-ms-input-placeholder {
  opacity: 0;
  font-style: normal;
}

.group-field-input-wrapper:hover .form-input::-ms-input-placeholder {
  opacity: 0;
  font-style: normal;
}

.group-field-input-wrapper:hover .form-input::placeholder {
  opacity: 0;
  font-style: normal;
}

.group-field-header {
  text-align: center;
}

.group-field-expanded-header-divider {
  border: 1px solid var(--light-green);
  background-color: var(--light-green);
}

.group-entries-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  justify-content: center;
}

.group-entry-container {
  width: 100%;
  text-align: center;
}

.group-entry-container:hover:not(.group-entry-container-active) {
  background-color: var(--main-green);
  color: var(--main-blue);
}

.group-entry-container-active {
  color: var(--main-green);
  pointer-events: none;
}

.group-entry-container-active:hover {
  color: var(--white-green);
}

.group-name {
  text-decoration: none;
  margin: 0.3em 0;
}

.member-form-row {
  display: flex;
  margin: 0.3em;
  justify-content: space-between;
  align-content: center;
}

.member-name-input-wrapper {
  flex: auto;
  margin-right: 1rem;
}

.member-form-submit {
  box-sizing: border-box;
  padding-top: 0.3em;
  margin-right: 0.3em;
}

.member-form-submit i {
  font-size: 3em;
  color: var(--iron);
  opacity: 0.5;
}

.member-form-submit.can-submit i {
  color: var(--main-green);
  opacity: 1;
}

.member-list {
  box-sizing: border-box;
  width: 100%;
  min-height: 30vh;
  padding: 1.25em 0.5em;
}

.member-container {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.05em;
}

.member-container:last-child {
  margin-bottom: 0;
}

.member-overview {
  flex: auto;
  /* display: flex; */
  /* flex-direction: column; */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: 0.5em;
  /* align-items: center; */
}

.member-icon {
  margin: 0 0.5em;
  font-size: 2rem;
}

.member-list-empty,
.member-list-empty-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.member-list-empty-text {
  font-weight: 400px;
}

.member-list-empty-icon {
  font-size: 10em;
}

.nav-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  float: none;
  width: 100%;
  height: 3rem;
  padding: 0.25em 0;
}

.nav-button {
  font-family: inherit;
  text-decoration: none;
  color: var(--main-blue);
  background: none;
  box-sizing: border-box;

  padding: 0.3rem 1rem;
  border-radius: 1.5rem;
  border: 1.3px transparent solid;

  width: 7.5rem;
  text-align: center;
}

.nav-button:hover {
  color: var(--white-smoke);
  background-color: var(--lighter-blue);
  border-color: var(--lighter-blue);
  cursor: pointer;
}

.nav-active {
  /* border-color: var(--white-smoke); */
  background-color: var(--main-blue);
  color: var(--white-smoke);
}

.expense-form-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin-bottom: 0.35em;
}

.expense-form-row:last-child {
  margin-bottom: 0;
}

.expense-form-input.description-input {
  flex: 2 1;
  margin-right: 0.35em;
}

.expense-form-input.amount-input {
  text-align: right;
  flex: 1 1;
}

.name-select {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  margin-right: 0.35em;
  color: var(--white-smoke);
}

.expense-form-submit {
  box-sizing: border-box;
  padding-top: 0.3em;
  margin-right: 0.3em;
}


.expense-form-submit i {
  font-size: 2em;
  color: var(--iron);
  opacity: 0.5;
}

.expense-form-submit.can-submit i {
  color: var(--main-green);
  opacity: 1;
}

.expense-list {
  box-sizing: border-box;
  width: 100%;
  min-height: 30vh;
  padding: 1.25em 0.5em;
}

.expense-container {
  margin: 0 0.65em;
  box-sizing: border-box;
  margin-bottom: 1.3em;
}

.expense-container:last-child {
  margin-bottom: 0;
}

.expense-header {
  font-family: var(--montserrat);
  font-size: 1.25em;
  color: var(--main-green);
  /* text-decoration: underline; */
  padding-bottom: 0.1em;
}

.expense-description {
  font-weight: 700;
}

.expense-details {
  font-style: oblique;
  font-family: var(--noto-sans);
  padding-left: 0.3em;
}

.expense-name-detail {
  padding: 0 0.2em;
  text-decoration: underline;
  font-style: normal;
  font-family: var(--montserrat);
}

.suggestion-list {
  box-sizing: border-box;
  width: 100%;
  min-height: 30vh;
  padding: 1.25em 0.5em;
}

.suggestion {
  font-size: 1.25em;
  margin: 0 0.85em;
  padding-bottom: 0.90em;
  font-family: var(--noto-sans);
}

.suggestion-detail {
  font-weight: 600;
  font-size: 1.15em;
  font-family: var(--montserrat);
  color: var(--main-green);
}

